.card {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: inherit;
    height: inherit;
}

h1 {
    margin-bottom: 10px;
}

span {
    margin-bottom: 10px;
}

.span {
    transition: 0.25s ease-in-out;
}
.container-span {
    display: inline-block;
}

.power {
    animation: scaleUpDown 0.25s reverse ease-in-out;
}


@keyframes scaleUpDown {
    0% {
        transform: scale(1);
    }

    100% {
        transform: scale(6);
    }
}

button {
    padding: 15px 25px;
    border: none;
    border-radius: 10px;
    margin-top: 10px;
    cursor: pointer;
    color: white;
    background-color: green;
    font: inherit;
}